import CartEmpty from "components/cartEmpty/CartEmpty";
import { useAuth } from "contexts/auth-context";
import { useShopee } from "contexts/shopeeContext";
import Footer from "modules/footer/Footer";
import Header from "modules/header/Header";
import Payment from "modules/payment/Payment";
import PaymentMobile from "modules/payment/PayMentMobile";
import React from "react";
import { useEffect } from "react";

const PaymentPage = () => {
  const { cartItems } = useShopee();
  useEffect(() => {
    document.title = "Thanh toán";
  }, []);

  return (
    <div className="flex flex-col min-h-screen overflow-hidden bg-whiteSmoke">
      <Header
        className1="bg-primary "
        className2="justify-between"
        hide={false}
      ></Header>
      <div className="flex-1">
        {cartItems.length > 0 ? (
          <div>
            <div className="hidden sm:block">
              <Payment></Payment>
            </div>
            <div className="block sm:hidden">
              <PaymentMobile></PaymentMobile>
            </div>
          </div>
        ) : (
          <CartEmpty></CartEmpty>
        )}
      </div>
      <Footer></Footer>
    </div>
  );
};

export default PaymentPage;
