import { Checkbox } from "components/checkbox";
import { useShopee } from "contexts/shopeeContext";
import ItemFav from "modules/shopeeContainer/ShopeeItem/parts/ItemFav";
import React from "react";
import { Fragment } from "react";
import { useState } from "react";

const PaymentItem = (props) => {
  const { control, data, checkAll } = props;

  const { removeCart } = useShopee();

  return (
    <Fragment>
      <div className="hidden p-4 bg-white rounded sm:block">
        <div className="flex items-center justify-between p-4 border border-gray-200 rounded">
          <div className="flex items-center gap-x-3">
            <div className="w-[80px] h-[80px] rounded overflow-hidden">
              <img
                className="object-center w-full h-full"
                src={data.img_url}
                alt={data.title}
              />
            </div>
            <h3 className="text-sm leading-[16px] text-[rgba(0,_0,_0,_0.87)] inline-block w-[230px] text-limit">
              {data.title}
            </h3>
          </div>
          <div className="grid items-center w-2/4 grid-cols-3">
            <div className="text-[rgba(0,_0,_0,_0.87)] text-[12px] leading-[17px] ml-3">
              <span className="underline">đ</span>{" "}
              <span className="text-[14px]">{data.price}</span>
            </div>

            <div className="text-primary text-[12px] leading-[17px ml-2">
              <span className="underline">đ</span>{" "}
              <span className="text-[14px]">{data.price}</span>
            </div>
            <div className="ml-5">
              <button
                onClick={() => removeCart(data.id)}
                className="text-sm text-[rgba(0,_0,_0,_0.87)] hover:text-primary transition-all"
              >
                Xóa
              </button>
            </div>
          </div>
        </div>
      </div>
      {/* Mobile */}
      <div className="block p-4 bg-white rounded sm:hidden">
        <div className="flex flex-col p-4 border border-gray-200 rounded">
          <div className="flex items-center gap-x-3">
            <div className="w-[80px] h-[80px] rounded overflow-hidden">
              <img
                className="object-center w-full h-full"
                src={data.img_url}
                alt={data.title}
              />
            </div>
            <div className="flex flex-col justify-between gap-y-5">
              <h3 className="text-sm leading-[16px] text-[rgba(0,_0,_0,_0.87)] inline-block w-[230px] text-limit-one-line">
                {data.title}
              </h3>
              <div className="flex">
                <div className="text-primary text-[12px] leading-[17px ml-2">
                  <span className="underline">đ</span>{" "}
                  <span className="text-[14px]">{data.price}</span>
                </div>

                <div className="ml-5">
                  <button
                    onClick={() => removeCart(data.id)}
                    className="text-sm text-[rgba(0,_0,_0,_0.87)] hover:text-primary transition-all"
                  >
                    Xóa
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default PaymentItem;
