import { Checkbox } from "components/checkbox";
import { useShopee } from "contexts/shopeeContext";
import React from "react";
import { useForm } from "react-hook-form";
import PaymentItem from "./PaymentItem";

const PaymentMobile = () => {
  const { control } = useForm({
    mode: "onChange",
  });
  const {
    cartItems,
    checkAll,
    hanleCheckAll,
    removeCartAll,
    totalMoney,
    buyItem,
  } = useShopee();
  const { id } = cartItems;

  return (
    <div className="flex flex-col gap-y-2  mx-auto mt-[50px] custom-scroll w-screen">

   
      <div className="">
        {cartItems.length > 0 &&
          cartItems.map((item) => {
            return (
              <PaymentItem
                data={item}
                key={item.id}
                control={control}
              ></PaymentItem>
            );
          })}
        {cartItems.length > 0 && (
          <>
            <div className="flex-col px-4 py-4 mb-10 bg-white rounded">
              <div className="flex flex-col gap-y-4">
                <div className="flex items-center cursor-pointer gap-x-2 text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)]">
                  <div className="mr-4 capitalize select-none">
                    <Checkbox
                      name="checkAll"
                      control={control}
                      checked={checkAll}
                      onClick={hanleCheckAll}
                    >
                      Chọn tất cả ({cartItems.length})
                    </Checkbox>
                  </div>

                  <button
                    onClick={() => removeCartAll(id)}
                    className="text-primary"
                  >
                    Xóa
                  </button>
                </div>
                <div className="flex items-center  gap-x-[30px]">
                  <div className="text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)] flex items-center">
                    Tổng thanh toán ({cartItems.length} Sản Phẩm):{" "}
                    <span className="text-primary text-[22px] w-[110px] block">
                      <span className="text-xs underline">đ </span>
                      {totalMoney}
                    </span>
                  </div>
                </div>
              </div>
              <div className="mx-auto mt-10 ">
                <button
                  onClick={buyItem}
                  className="p-3 text-white bg-primary h-[40px] w-full rounded items-center justify-center flex "
                >
                  Mua hàng
                </button>
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default PaymentMobile;
