import React from "react";
import { Link } from "react-router-dom";

const CartEmpty = () => {
  return (
    <div className="w-[200px] h-[350px]  text-center  relative left-2/4 -translate-x-2/4 mt-[100px] ">
      <div>
        <img src="./cartEmpty.png" alt="" className="w-full h-full" />
      </div>
      <p className="text-sm text-[rgba(0,_0,_0,_0.4)] font-[600] my-5">
        Giỏ hàng bạn còn trống
      </p>
      <Link to="/">
        <button className="p-3 w-[150px] text-white capitalize rounded bg-primary">
          Mua ngay
        </button>
      </Link>
    </div>
  );
};

export default CartEmpty;
