import { Checkbox } from "components/checkbox";
import { useShopee } from "contexts/shopeeContext";
import React from "react";
import { useForm } from "react-hook-form";
import PaymentItem from "./PaymentItem";

const Payment = () => {
  const { control } = useForm({
    mode: "onChange",
  });
  const {
    cartItems,
    checkAll,
    hanleCheckAll,
    removeCartAll,
    totalMoney,
    buyItem,
  } = useShopee();
  const { id } = cartItems;

  return (
    <div className="flex flex-col gap-y-2 max-w-[1280px] mx-auto mt-[50px] px-5 custom-scroll ">
      {/* h-[500px] overflow-y-auto */}

      <div className="flex items-center justify-between py-4 pl-8 pr-6 capitalize bg-white rounded ">
        <div className="flex gap-x-3 text-[rgba(0,_0,_0,_0.8)] text-[14px] leading-[17px] select-none">
          Sản phẩm
        </div>
        <div className="grid w-2/4 grid-cols-3 gap-y-4 text-sm leading-[17px] text-[#888888]">
          <div>Đơn Giá</div>
          <div>Số tiền</div>
          <div>Thao tác</div>
        </div>
      </div>
      <div className="">
        {cartItems.length > 0 &&
          cartItems.map((item) => {
            return (
              <PaymentItem
                data={item}
                key={item.id}
                control={control}
              ></PaymentItem>
            );
          })}
        {cartItems.length > 0 && (
          <>
            <div className="items-center justify-between px-8 py-4 mb-10 bg-white rounded sm:hidden lg:flex">
              <div className="flex items-center cursor-pointer gap-x-2 text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)]">
                <div className="mr-4 capitalize select-none">
                  <Checkbox
                    name="checkAll"
                    control={control}
                    checked={checkAll}
                    onClick={hanleCheckAll}
                  >
                    Chọn tất cả ({cartItems.length})
                  </Checkbox>
                </div>

                <button
                  onClick={() => removeCartAll(id)}
                  className="text-primary"
                >
                  Xóa
                </button>
              </div>
              <div className="flex items-center w-[50%] gap-x-[30px]">
                <div className="text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)] flex items-center">
                  Tổng thanh toán ({cartItems.length} Sản Phẩm):{" "}
                  <span className="text-primary text-[22px] w-[110px] block">
                    <span className="text-xs underline">đ </span>
                    {totalMoney}
                  </span>
                </div>
                <button
                  onClick={buyItem}
                  className="p-3 text-white bg-primary h-[40px] w-[180px] rounded items-center justify-center flex "
                >
                  Mua hàng
                </button>
              </div>
            </div>
            {/* Tablet */}
            <div className="flex-col px-6 py-4 mb-10 bg-white rounded sm:flex lg:hidden">
              <div className="flex items-center justify-between">
                <div className="flex items-center cursor-pointer gap-x-2 text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)]">
                  <div className="mr-4 capitalize select-none">
                    <Checkbox
                      name="checkAll"
                      control={control}
                      checked={checkAll}
                      onClick={hanleCheckAll}
                    >
                      Chọn tất cả ({cartItems.length})
                    </Checkbox>
                  </div>

                  <button
                    onClick={() => removeCartAll(id)}
                    className="text-primary"
                  >
                    Xóa
                  </button>
                </div>
                <div className="flex items-center w-[55%] gap-x-[30px]">
                  <div className="text-[16px] leading-[19px] text-[rgba(0,_0,_0,_0.8)] flex items-center">
                    Tổng thanh toán ({cartItems.length} Sản Phẩm):{" "}
                    <span className="text-primary text-[22px] w-[110px] block">
                      <span className="text-xs underline">đ </span>
                      {totalMoney}
                    </span>
                  </div>
                </div>
              </div>
              <div className="mx-auto mt-10 ">
                <button
                  onClick={buyItem}
                  className="p-3 text-white bg-primary h-[40px] w-[250px] rounded items-center justify-center flex "
                >
                  Mua hàng
                </button>
                
              </div>
            </div>
          </>
        )}
      </div>
    </div>
  );
};

export default Payment;
